import Swiper from 'react-id-swiper'
import { useSelector } from 'react-redux'
import { Pagination, Autoplay } from 'swiper'
import { WELCOME_SWIPER } from '../../../assets/image'

export default function ChatWelcome() {
    const theme = useSelector(state => state.theme)

    const toggleViewMessage = () => {
        if (theme?.isHidden && !false) return 'hidden not-toggle-mess'
        if (!theme?.isHidden) return 'not-hidden'
    }

    const data = [
        {
            img: WELCOME_SWIPER[0],
            title: 'Nhn tin nhiu hn, son tho t hn',
            desc: 'S dng tin nhn nhanh  lu sn cc tin nhn thng dng v gi nhanh trong hi thoi bt k.',
        },
        {
            img: WELCOME_SWIPER[1],
            title: 'Tin nhn t ng xa',
            desc: 'Tin nhn t ng xa sau mt thi gian nht nh,  bn khng b mt tin nhn.',
        },
        {
            img: WELCOME_SWIPER[2],
            title: 'Chat nhm vi ng nghip',
            desc: 'Chat nhm vi ng nghip vi cc tin nhn c bit, nhn tin nhm  gi tin nhn n nhiu ngi.',
        },
    ]

    const params = {
        slidesPerView: 'auto',
        autoplay: {
            delay: 2500,
            disableOnInteraction: false,
        },
        loop: true,
        navigation: {
            nextEl: '.swiper-next',
            prevEl: '.swiper-prev',
        },
        renderPrevButton: () => (
            <div className="swiper-prev dark:text-gray-100 dark:hover:text-blue-600 hover:text-blue-600">
                <ion-icon name="chevron-back-outline"></ion-icon>
            </div>
        ),
        renderNextButton: () => (
            <div className="swiper-next dark:text-gray-100 dark:hover:text-blue-600 hover:text-blue-600">
                <ion-icon name="chevron-forward-outline"></ion-icon>
            </div>
        ),
        pagination: {
            el: '.swiper-pagination',
            clickable: true,
        },
        modules: [Pagination, Autoplay],
        containerClass:
            'swiper-container h-full chatwelcome overflow-hidden lg:max-w-sm llg:max-w-none mx-auto',
    }

    return (
        <div className={`view-message ${toggleViewMessage()}`}>
            <div className="py-20 h-full overflow-hidden flex flex-col">
                <div className="mb-4 px-10">
                    <h1 className="text-center text-xl mb-4 dark:text-gray-200 font-light">
                        Cho mng n vi{' '}
                        <span className="font-medium">ChatVia!</span>
                    </h1>

                    <p className="text-center text-sm line-clamp-2 dark:text-gray-300">
                        Khm ph nhng tin ch h tr lm vic v tr chuyn
                        cng ngi thn, bn b c ti u ho cho my tnh ca
                        bn.
                    </p>
                </div>

                <div className="px-10 relative">
                    <Swiper {...params}>
                        {data.map((item, index) => (
                            <div
                                key={index}
                                className="select-none"
                                style={{ height: 'auto' }}
                            >
                                <div className=" flex items-center justify-between flex-col h-full ">
                                    <img
                                        src={item.img}
                                        alt=""
                                        className="object-contain w-96"
                                    />
                                    <div>
                                        <h2 className="text-lg text-center text-blue-600 font-medium line-clamp-2 my-4 w-full">
                                            {item.title}
                                        </h2>
                                        <p className="text-sm text-center line-clamp-2 leading-6 h-12 w-full dark:text-gray-300">
                                            {item.desc}
                                        </p>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </Swiper>
                </div>
            </div>
        </div>
    )
}
